import { useState, useEffect } from "react";
import { InputText, Form, BtnSubmit } from "../atoms";
import { MovieList, Pagination  } from "../organisms";
import { getMovieList } from "../../apis/movie"; // default   {}  .
import { countryList } from "../../datas"; // Select List

const Movie = () => {
  const [page, setPage] = useState(1); //Pagination   page . (props )
  const [country, setCountry] = useState(countryList[0].code);  //country   searchMovie() 
  const [value, setValue] = useState("");
  const [query, setQuery] = useState("");
  const [movieList, setMovieList] = useState([]);
  const [total, setTotal] = useState(0); // items 


  useEffect(() => {
    searchMovie();
  }, [country, page, query]);//country, page, query   searchMovie(); 
  //value    setValue(e.target.value)  (  )

//setPage, setQuery    useEffect 1 .
  const handleSubmit = (e) => {
    e.preventDefault(); 
    setPage(1);//      .
    setQuery(value); //query value  . 
  };

  //api 
  const searchMovie = async () => {
    if (!query) return; //value  return =>   400 .
    const start = page * 10 - 9; // 1=1~10, 2=11~20, 3=21~30, 4=31~40, 5=41~50...(  .)
    //const params = {query: query(=value), country : country, start= page * 10 - 9;}
    const params = { query, start};
    if (country !== "ALL") {
      params.country = country; //All   params country 
    }
    const { items, total } = await getMovieList(params); // getMovieList  async/awiat 
    setMovieList(items);
    setTotal(total);
  };

  return (
    <div>
      <h1>movie</h1>
      <Form onSubmit={handleSubmit}>
      <select onChange={(e) => setCountry(e.target.value)}>
          {countryList.map(({ code, name }) => (
            <option value={code} key={code}>
              {name}
            </option>
          ))}
        </select>
        <InputText onChange={(e) => setValue(e.target.value)} />
        <BtnSubmit></BtnSubmit>
      </Form>
      <MovieList data={movieList} />
      <Pagination 
      total={total} 
      onPageChange={(page) => setPage(page)} 
      nowPage={page} 
      />
    </div>
  );
};

export default Movie;