import styled, { css } from "styled-components";

const Pagination = ({ total, onPageChange, nowPage }) => {
  const lastPage = Math.ceil(total / 10); // (p) . // 100   .
  // 1 - 1p / 10 - 1p // 12 - 2p / 20 - 2p / 91 - 10p / 100 - 10p
  
  const startPage =Math.ceil(nowPage/10) * 10 - 9;  
  //1~10 : 1  / 11~20 : 11  / 21~30 : 21  / 91~100 : 91 

  //lastPage startPage + 9   lastPage    startPage + 9 .
  // (startPage(11) + 9) > lastPage(18) ? lastPage : startPage + 9; =>lastPage
  // (startPage(11) + 9) > lastPage(20) ? lastPage : startPage + 9; =>startPage + 9 
  const endPage = (startPage + 9) > lastPage ? lastPage : startPage + 9;
  
  const pageList = [];
  //console.log(lastPage, nowPage)
  //map    .
  for(let i = startPage; i <= endPage; i++){
    pageList.push(i)
  }

  return (
    <List>
      {nowPage > 1 && (
        <Item onClick={() => {onPageChange(nowPage - 1)}}></Item>
        )}
      {pageList.map((page)=>(
        <Item 
        isActive={nowPage === page}
        onClick={() => {onPageChange(page)}} 
        key={page}
        >
        {page}
      </Item>
      ))}
      {nowPage < lastPage && (
      <Item onClick={() => {onPageChange(nowPage + 1)}}></Item>
        )}
    </List>
  );
};

const List = styled.ul`
  display: flex;
`;
const Item = styled.li`
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #000;
  border-radius: 50%;
  margin: 5px;
  cursor: pointer;
  :hover {
    background: #000;
    color: #fff;
  }
  /* //1 
  background: ${({isActive})=> (isActive && "#000")};
  color: ${({isActive})=> (isActive && "#fff")}; */

  //2
  ${({isActive}) =>
  isActive && 
  css`
   background:#000;
   color : #fff;
  `}

  ${({next}) =>
  next && 
  css`
   display: none;
  `}

  ${({prev}) =>
  prev && 
  css`
   display: none;
  `}
`;
export default Pagination;
